<template>
  <div v-if="this.user && !loading">
    <div class="flex-j-center">
      <Card :title="this.user.first_name + ' ' + this.user.last_name">
        <div class="flex-j-center">
          <img style="border-radius: 50%" :src="this.user.avatar" alt="" />
        </div>
        <br>
        <table
          style="margin-bottom: 10px"
          v-for="field in this.fields"
          :key="field"
        >
          <tbody>
            <th>
              {{ (field[0].toUpperCase() + field.substr(1)).replace("_", " ") }}:
            </th>
            <td>{{ user[field] }}</td>
          </tbody>
        </table>
      </Card>
    </div>
  </div>
  <div v-else-if="!this.user && !loading">Not found</div>
</template>

<script>
import { parseParams } from "../tools/params.js";
import Card from "../components/Card.vue";
export default {
  name: "User",
  data() {
    return {
      id: parseParams(window.location.href.split("?")[1]).id,
      loading: true,
    };
  },
  async mounted() {
    var res = await fetch("https://reqres.in/api/users/" + this.id)
      .then((r) => r)
      .then((r) => r.json());

    this.user = res.data;
    this.loading = false;
    this.fields = Object.keys(this.user).filter((k) => k != "id"&& k!="avatar");
  },
  components: { Card },
};
</script>

