<template>
  <component v-if="this.currentRoute" :is="this.currentRoute.component" />
  <div v-else>
    <h1 class="t-center">Page you are looking for is not found</h1>
  </div>
</template>

<script>
import Home from "../views/Home.vue";
import UsersVue from "../views/Users.vue";
import User from "../views/User.vue";
import HelloWorldVue from "./HelloWorld.vue";

function parseParams(s) {
  s = s.split("&");
  var parsed = {};

  for (var i = 0; i < s.length; i++) {
    var param = s[i];

    parsed[param.split("=")[0]] = decodeURIComponent(param.split("=")[1]);
  }

  return parsed;
}

export default {
  name: "Router",
  components: { Home, HelloWorldVue },
  data() {
    var _routes = [
      {
        path: ["/home", "/"],
        name: "Home",
        component: Home,
      },
      {
        path: ["/users", "users"],
        name: "Users",
        component: UsersVue,
      },
      {
        path: ["/user", "users"],
        name: "User",
        component: User,
      },
    ];

    return {
      loading: true,
      routes: _routes,
      currentRoute: _routes[0],
    };
  },
  methods: {
    changeRoute() {
      var path = window.location.pathname;
      var params = window.location.href.split("?")[1];
      var route = this.routes.find((r) => r.path.includes(path));

      if (params && route) {
        params = parseParams(params);
        if ("url" in params) {
          var otherKeys = Object.keys(params).filter((k) => k != "url");
          var otherParams = {};
          var otherParamsVal = [];

          otherKeys.forEach((k) => {
            otherParams[k] = params[k];
            otherParamsVal.push(params[k]);
          });

          route = this.routes.find((r) => r.path.includes(params.url));
          history.replaceState(
            {},
            route.name,
            otherKeys.length>0
              ? route.path[0] +
                  "?" +
                  new URLSearchParams(otherParams).toString()
              : route.path[0]
          );
        }
      }

      if (route) {
        document.title = route.name;
      }
      this.currentRoute = route;
    },
  },
  mounted() {
    this.loading = false;

    document.body.addEventListener("click", (e) => {
      if (e.target.hasAttribute("data-link")) {
        e.preventDefault();
        console.log("link")
        history.pushState(null, null, e.target.getAttribute("href"));
        this.changeRoute();
      }
    });

    window.addEventListener("popstate", () => {
      this.changeRoute();
    });

    this.changeRoute();
  },
};
</script>

