<template>
  <div :data-key="this.i" class="link">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "Link",
  data() {
    return {
      to: this.link,
      i: Math.floor(Math.random()*100000)
    };
  },
  methods: {
    setLink(element, link) {
      element.setAttribute("data-link", null);
      element.setAttribute("href", link);
    },
  },
  mounted() {
    this.$nextTick(() => {
      var link = this.to;
      function setLink(element, link) {
        if (element.setAttribute) {
          element.setAttribute("data-link", null);
          element.setAttribute("href", link);
          element.childNodes.forEach((element) => {
            setLink(element, link);
          });
        }
      }
      var p = document.querySelector("[data-key='" + this.i+"']");

      if (p) {
        p.childNodes.forEach((element) => {
          setLink(element, link);
        });
      }
    });
  },
  props: ["link"],
};
</script>

