<template>
  <h1 class="t-center">Users</h1>
  <br />
  <div v-for="i in items" :key="i.id">
    <div class="flex-j-center">
      <LinkTo :link="this.getUrl('/user',{id:i.id})">
      <Card :title="i.first_name + ' ' + i.last_name">
        <div class="flex-j-center">
          <img style="border-radius:50%" :src="i.avatar" alt="" />
        </div>
        <p class="t-center">
          Press for more info
        </p>
      </Card>
      </LinkTo>
    </div>
    <br /><br />
  </div>
</template>

<script>
import Card from "../components/Card.vue";
import LinkTo from '../components/Link.vue';
import { getUrl } from '../tools/getUrl';


export default {
  name: "Users",
  data() {
    return {
      items: [],
      page: 1,
      getUrl
    };
  },
  components: { Card,LinkTo },
  async mounted() {
    if (this.page != null) {
      var res = await fetch("https://reqres.in/api/users?page=" + this.page)
        .then((r) => r)
        .then((r) => r.json());

      this.items = res.data;
      this.page = res.page;
    }
  },
};
</script>

